*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
body{
    background-color: rgb(17, 17, 24);
    color: whitesmoke;
}
.app{
     width: 40%;
     max-width: 400px;
     min-width: 350px;    
     margin: 5rem auto;
}
.profile{
    text-align: center;
}
.profile>h1{
    margin-top: 1rem;
}
.profile>p{
    margin-top: 0.7rem;
}
.contact-container>a{
    color: black;
    text-decoration: none;
    font-weight: 550;
}
.contact{
    margin-top: 2em;
    padding: 0.5rem 0;
    width: 100%;
    background-color: whitesmoke;
    border: none;
    border-radius: 0.3rem;
    cursor: pointer;
    transition: ease-in-out 400ms;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}
.contact>span{
    font-size: medium;
    margin-left: 0.3rem;
    
}
.contact:hover{
    background-color: rgb(216, 210, 210);
    transform: translateY(-1px);
    color: rgb(17, 17, 24);
}

.maincontent{
    width: 80%;
    margin: 0 auto;
}

.interests, .about{
    margin-top: 1.7rem ;
}

.interests>p,.about>p{
    margin-top: 0.5rem;
    color: rgb(221, 211, 221);
}


.socials>ul{
list-style: none;
display: flex;
justify-content: space-between;
width: 80%;
margin: 0 auto;
margin-top: 1.8rem;
}


.socials>ul>li{
    transition: ease-in-out 300ms;

}

.socials>ul>li:hover{
    transform: translateY(-5px)
}

.socials-fill{
    fill:white;
    width: 35px;
    height: 35px;
}
.about>p,.interests>p{
text-indent: 2%;
}